import React, { Component } from 'react'
import Fade from 'react-reveal/Fade'
import Zoom from 'react-reveal/Zoom'

class About extends Component {
  render () {
    return(
      <div className='about-page'>

        <div className='about-text' key='unique'>
          <Zoom cascade top><h1>About me</h1></Zoom>
          <Fade left delay={200}>
            <p>
              My first encounter with HTML and some basic CSS was back in middle school. After middle school I attended Technical High School for Computer Science where my knowledge of these technologies got a bit more expanded. And even though I enjoyed coding, after graduating I wasnt sure what would be the job of my dreams.
            </p>
          </Fade>
          <Fade left delay={350}>
            <p>
              After a bumpy road I finally decided to become Front End Developer. A friend recommended to learn using Udacitys platform (which contains a lot of online courses and some of them result in obtaining a certificate).  To complete the program I had to build several projects and have them accepted by reviewers.
            </p>
          </Fade>
        </div>

        <div className='about-certificate'>
          <a href='https://confirm.udacity.com/HEGVMXKA'>
            <Zoom delay={300}>
              <img src='/portfolio/img/fend-certificate.svg' alt='Front End Nanodegree Certificate'/>
            </Zoom>
          </a>
        </div>
      </div>
    )

  }
}

export default About;



